.progressContainer {
  margin-top: 50px;
}

.totalCount {
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.progressBar {
  margin-top: 20px;
  height: 15px;
  margin-bottom: 20px;
}

.detailData {
  display: flex;
  // margin-top:20px;
  gap: 80px;
}

.dataItem {
  font-size: 1rem;
}
.text-status {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
  // color: #d0d1d2;
  gap: 10px;
  margin-bottom: 10px;
}
.colorIndicator {
  width: 40px;
  height: 5px;
  border-radius: 100px;
  margin-right: 10px;
}
.text-total-line {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
}

.progressBar {
  display: flex;
  width: 100%;
  height: 15px;
  border-radius: 15px;
  overflow: hidden;
}

.progressFill1 {
  height: 100%;
  border-radius: 15px;
}

.progressFill2 {
  height: 100%;
  border-radius: 0 15px 15px 0;
  margin-left: -15px;
}
